import { Pheading } from "../../widgets/Pheading"
import {useSelector} from "react-redux"

export const ShoppingDetail = () =>{
    const product = useSelector(state=>state.product)
    let productAmount = (product.map(i => {
        return (i.amount*parseInt(i.itemQty))
    })).reduce((total, arg) => total + parseInt(arg), 0);

    let totalTax = (product.map(i =>{
        return (i.tax.split('%')[0])/100
    })).reduce((total, arg) => total + parseFloat(arg), 0);
    return (
        <>
        <div class="ShoppingCart">
            <h3 style={{fontSize:'35px'}}>Your Shopping</h3>
            <div class="shoppingDetail">
                <Pheading heading="Sub Total" value={productAmount}/>
                <Pheading heading="Total Tax" value={(productAmount*totalTax).toFixed(2)}/>
                <Pheading heading="Total Amount" value={(productAmount+parseFloat((productAmount*totalTax).toFixed(2))).toFixed(2)}/>
                {/* <Pheading heading="Discount" value="3814.83"/>
                <Pheading heading="Net Amount" value="3814.83"/>
                <Pheading heading="Recieved Amount" value="3814.83"/>
                <Pheading heading="Return Amount" value="3814.83"/> */}
            </div>
        </div>
        </>
    )
}