import { H2heading } from "../widgets/H2heading"
import { Input } from "../widgets/Input"
import './subComponent.css';
export const UpBottomSub = () =>{
    return(<>
        <div class="upBottom" style={{textAlign: "center"}}>
            <div class="row">
                <div class="col span-1-of-4 upBottomData">
                    <H2heading h2Heading="Bill No."/>
                    <p style={{marginBottom:0}}>10001</p>
                </div>
                <div class="col span-1-of-4 upBottomData">
                    <h2>Customer</h2>
                    <select name="cars" id="cars">
                        <option>Select</option>
                        <option value="18">Cash</option>
                        <option value="12">Debit Card</option>
                    </select>
                </div>
                <div class="col span-1-of-4 upBottomData">
                    <H2heading h2Heading="Mobile No."/>
                    <Input inputType="text"/>
                </div>
                <div class="col span-1-of-4 upBottomData">
                    <H2heading h2Heading="GST No."/>
                    <Input inputType="text"/>
                </div>
            </div>
        </div>
    </>)
}