import { H2heading } from "../widgets/H2heading"
import { Input } from "../widgets/Input"

export const MiddleSub = (props) => {
    return (
        <>
        <div class="col span-1-of-3" style={{margin: 0}}>
            <H2heading h2Heading={props.h2heading}/>
            <Input inputType={props.inputType}/>
        </div>
        </>
    )
}