import { DataGrid} from '@mui/x-data-grid';
import {useSelector} from "react-redux"
// eslint-disable-next-line no-unused-vars

export const BillingTable = () =>{
  const productRows = useSelector(state=>state.product);
  const columns= [
      { field: 'itemCode', headerName: 'Item_Code', width: 130 },
      { field: 'itemName', headerName: 'Item_name', width: 130 },
      { field: 'itemQty', headerName: 'Quantity', width: 130 },
      { field: 'itemType', headerName: 'Item_type', width: 130 },
      { field: 'unit', headerName: 'Unit', width: 130 },
      { field: 'amount', headerName: 'Amount', width: 130 },
      { field: 'tax', headerName: 'Tax', width: 130 },
      { field: 'total', headerName: 'Total', width: 130 },
    ];
    
    const rows = productRows
    return (
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid
            rows={rows}
            columns={columns}
            pageSize={5}
            rowsPerPageOptions={[5]}
            checkboxSelection
            onSelectionModelChange={(ids) => {
              const selectedIDs = new Set(ids);
              // console.log(rows)
              const selectedRows = rows.filter((row) =>
                selectedIDs.has(row.id),
              );
    
              console.log(selectedRows)
            }}
          />
        </div>
      );
}