import { store } from "../../redux/store";
import { addData } from "../../redux/actions/billingAction";
import {useRef } from "react";

export const SubTopMiddleBottom = (props) =>{
    const item = useRef("");
    const items = () => {
        if(props.name === "Item Code"){
            const action = addData("ADD_CODE",item.current.value)
            store.dispatch(action)
        }else if(props.name === "Item Name"){
            const action = addData("ADD_ITEM",item.current.value)
            store.dispatch(action)
        }else if(props.name === "Qty"){
            const action = addData("ADD_QTY",item.current.value)
            store.dispatch(action)
        }else if(props.name === "Amount"){
            const action = addData("ADD_AMOUNT",item.current.value)
            store.dispatch(action)
        }else if(props.name === "Item Type"){
            const action = addData("ADD_TYPE",item.current.value)
            store.dispatch(action)
        }else if(props.name === "Unit"){
            const action = addData("ADD_UNIT",item.current.value)
            store.dispatch(action)
        }
        
    }

    if(props.name && props.value){
        return(
            <div class="col span-1-of-9">
                    <p>{props.name}</p>
                    <p>{props.result}</p>
            </div>
        );
    }else if(props.name && props.option) {
        if(props.option === "type"){
            return(
                <div class="col span-1-of-9">
                        <p>{props.name}</p>
                        <select ref={item} onChange={items}>
                        <option disabled selected value> -- select -- </option>
                            <option value="Fashion">Fashion</option>
                            <option value="Grocery">Grocery</option>
                        </select>
                </div>
            )
        }else{
            return(
                <div class="col span-1-of-9">
                        <p>{props.name}</p>
                        <select ref={item} onChange={items}>
                        <option disabled selected value> -- select -- </option>
                            <option value="ltr">ltr</option>
                            <option value="meter">meter</option>
                        </select>
                </div>
            )
        }
        
    }return (
        <>
            <div class="col span-1-of-9">
                    <p>{props.name}</p>
                    <input ref={item} type="text" onChange={items}/>
            </div>
        </>
    )
}