import { BillingTable } from "./BottomSubComponent/BillingTable"
import { PrintBill } from "./BottomSubComponent/PrintBill"
import { ShoppingDetail } from "./BottomSubComponent/ShoppingDetail"
import { TopMiddleBottom } from "./BottomSubComponent/TopMiddleBottom"

export const MiddleBottomSub = () => {
    return (
        <>
            <div class="middleBottom">
                    <div class="row">
                        <div class="col span-3-of-4">
                            <TopMiddleBottom/>
                            <BillingTable/>
                            <PrintBill/>
                        </div>
                        <div class="col span-1-of-4">
                            <ShoppingDetail/>
                        </div>
                    </div>
                </div>
        </>
    )
}