import { ButtonBottom} from "../../widgets/Button"
import { SubTopMiddleBottom } from "./SubTopMiddleBottom"
import { useSelector } from "react-redux"

export const TopMiddleBottom = () => {
    const gst = useSelector(state=>state.gst);
    const amount = useSelector(state=>state.amount);
    const qty = useSelector(state=>state.qty);
    return (<>
        <div class="topMiddleBottom">
            <div class="row">
                <SubTopMiddleBottom name="Item Code"/>
                <SubTopMiddleBottom name="Item Name"/>
                <SubTopMiddleBottom name="Qty"/>
                <SubTopMiddleBottom name="Item Type" option="type"/>
                <SubTopMiddleBottom name="Unit" option="unit"/>
                <SubTopMiddleBottom name="Amount"/>
                <SubTopMiddleBottom name="TAX (%)" result={gst} value="store"/>
                <SubTopMiddleBottom name="Tax Amount" result={amount*(gst/100)} value="store"/>
                <SubTopMiddleBottom name="Final Amount" result={parseInt(amount)*qty+parseInt(amount*(gst/100))} value="store"/>
            </div>
            <div class="row">
                <div class="col span-1-of-3">
                    <ButtonBottom buttonName="Add Item"/>
                </div>
                <div class="col span-1-of-3">
                    <ButtonBottom buttonName="Edit Item"/>
                </div>
                <div class="col span-1-of-3">
                    <ButtonBottom buttonName="Delete Item"/>
                </div>
            </div>
        </div>
    </>)
}