import { ButtonBottom } from "../../widgets/Button"
import { H4heading } from "../../widgets/H4heading"
import "../subComponent.css"


export const PrintBill = () => {
    return (
        <div class="printBill">
           <div class="row">
               <div class="col span-2-of-9">
                   <H4heading h4Heading="Total Item : 2"/>
               </div>
               <div class="col span-2-of-9">
                   <H4heading h4Heading="Total Item Quantity : 2"/>
               </div>
           </div>
           <div class="row">
               <div class="col span-2-of-9">
                   <ButtonBottom buttonName="Print Bill"/>
               </div>
           </div>
        </div>   
       
    )
}