import { store } from "../redux/store";
import { addItems } from "../redux/actions/billingAction";
import { useSelector } from "react-redux"

export const ButtonBottom = (props) =>{
    const itemCode = useSelector(state=>state.itemCode);
    const itemName = useSelector(state=>state.itemName);
    const qty = useSelector(state=>state.qty);
    const unit = useSelector(state=>state.unit)
    const type = useSelector(state=>state.itemType)
    const amount = useSelector(state=>state.amount)
    const gst = useSelector(state=>state.gst);
    const products = useSelector(state=>state.product);
    const id = useSelector(state=>state.id);
    const product = {id:id,itemCode: itemCode, amount: amount, itemQty:qty,itemType: type,itemName:itemName,unit:unit,tax:`${gst}%`,total:parseInt(amount)*qty+parseInt(amount*(gst/100))}
    const opr = () => {
        if(props.buttonName === "Add Item"){
            const action = addItems("ADD_PRODUCT",product)
            console.log(product,"Before Store")
            store.dispatch(action);
        }
        console.log(products,"iNSIDSKNFSDN")
    }

    return(
        <>
            <input type="button" value={props.buttonName} onClick={opr}/>
        </>
    );
}