import { MiddleSub } from "../subComponent/MiddleSub"
import {useRef} from "react";
import { getGST } from "../redux/actions/billingAction";
import {store} from "../redux/store"
export const Middle = () => {
    const gst = useRef("");
    const getValue = () =>{
        const action = getGST("GST",gst.current.value);
        store.dispatch(action)
    }
    return(<>
        <div style={{textAlign: "center", marginBottom:"15px"}}>
                <div class="row">
                    <MiddleSub h2heading="Date of Bill" inputType="date"/>
                    <MiddleSub h2heading="Time of Bill" inputType="time"/>
                    <div class="col span-1-of-3" style={{margin:0}}>
                        <h2>GST Type:</h2>
                        <select ref={gst} onChange={getValue}>
                        <option disabled selected value> -- select -- </option>
                            <option value="18">18%</option>
                            <option value="12">12%</option>
                        </select>
                    </div>
                </div>
            </div>
    </>)
}