export const reduce_action = (state={itemCode:"",itemName:"",qty:"",amount:0,itemType:"",unit:"",gst:0,id:0,product:[]}, action) =>{
    if(action.type === "ADD_CODE"){
        let temp = state.itemCode;
        temp = action.payload;
        return {...state,itemCode: temp}
    }else if(action.type === "ADD_ITEM"){
        let temp = state.itemName;
        temp = action.payload;
        return {...state,itemName: temp}
    }else if(action.type === "ADD_QTY"){
        let temp = state.qty;
        temp = action.payload;
        return {...state,qty: temp}
    }else if(action.type === "ADD_AMOUNT"){
        let temp = state.amount;
        temp = action.payload;
        return {...state,amount: temp}
    }else if(action.type === "ADD_TYPE"){
        let temp = state.itemType;
        temp = action.payload;
        return {...state,itemType: temp}
    }else if(action.type === "ADD_UNIT"){
        let temp = state.unit;
        temp = action.payload;
        return {...state,unit: temp}
    }else if(action.type === "GST"){
        let temp = state.gst;
        temp = action.payload;
        console.log(temp,"temp");
        return {...state,gst: temp}
    }else if(action.type === "ADD_PRODUCT"){
        let temp = state.product;
        let id = state.id;
        id = id+1
        temp = action.payload;
        console.log(action.payload,"temp",state.id);
        return {...state,...state.product,id:id, product:[...state.product, temp]}
    }
    return state
}